<template lang="pug">
.PathText  :  > {{pageTitle}}
    .QAContainer
        h1#listPageTitle {{pageTitle}}
        .markBlockContainer
            router-link.markBlock(style="color: #333; text-decoration: none;" v-for='(typ,t) in typeList' :key='t' :to="typ") {{typ}}
        .questionAndAnswer(v-for='QAs in theArticleList')
            .question Q.{{QAs.question}}
            br
            .answer {{QAs.answer}}
</template>
<script>
export default {
    data(){
        return{
            pageTitle: "",
            typeList: ["120","175","-20"],
            QAList: [
                {type: '120',question:'', answer:'AB'},
                {type: '120',question:'', answer:'A//B'},
                {type: '175',question:'', answer:''},
                {type: '175',question:'', answer:''},
                {type: '120',question:'', answer:'126'},
                {type: '120',uestion:'', answer:'70%'},
                {type: '120',question:'', answer:'CDDVD'},
                {type: '-20',question:'', answer:'1081231'}
            ],
            theArticleList: '',
            selectedQuery: '',
        }
    },
    beforeMount(){
        this.selectedQuery = this.$route.params.id;
    },
    watch: {
        selectedQuery: function(query) {
            this.theArticleList = this.QAList.filter((item)=>{
                return item.type.includes(query);
            })
        },
        theArticleList: function(item) {
            if(item.length==0){
                this.theArticleList = this.QAList;
            }
                
        }
    }
}
</script>
<style lang="sass">
.QAContainer
    max-width: 1280px
    min-height: 720px
    // border: 2px solid #549CF8
    margin: auto

.questionAndAnswer
    text-align: left
    padding: 10px
    font-size: 15px
    width: 85%
    margin: auto
    margin-bottom: 40px
    border: 1px solid #F2DCE0

.question
    font-size: 15px

.answer
    margin-left: 30px
</style>